body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

::-webkit-scrollbar-thumb {
    background: #8a8a8a;
    border-radius: 10px;
}

::-webkit-scrollbar {
    width: 5px;
    height: 5px;
}

.App,
#root {
    height: 100vh;
    width: 100vw;
    padding: 0;
    margin: 0;
    overflow-y: auto;
    overflow-x: hidden;
    font-family: "Source Sans Pro";
    /* background-color: #e0e0e0; */
    color: #555;
}

.cv-container {
    height: auto;
    width: 750px;
    margin: auto;
    border-radius: 4px;
}

.main--head {
    height: 40vh;
    width: 100vw;
    position: absolute;
    background-color: #21587f;
    z-index: -1;
}

.main--body {
    height: 60vh;
    width: 100vw;
    position: absolute;
    top: 40vh;
    background-color: #dedede;
    z-index: -1;
}

.mt-10 {
    margin-top: 10px;
}

.mlr-10 {
    margin: 0 15px;
}

.font-cap {
    text-transform: capitalize;
}

.cv-divider {
    height: 2.5px;
    background-color: #d0d0d0;
}

.r-flex {
    display: flex;
}

.r-flex--full {
    flex: 1;
}

.r-flex--col {
    flex-direction: column;
}

.r-flex-align--center {
    align-items: center;
}

.r-flex-just--center {
    justify-content: center;
}

.r-flex-just--around {
    justify-content: space-around;
}

.cv-contact--list a {
    color: #fff;
}