.cv-container.CVSingle {
    background-color: #ffffff;
    box-shadow: 0 2px 5px 0 #555353, 0 2px 10px 0 #646161;
}


/* .cv-content {
    padding-bottom: 10px;
} */

.cv-content--left {
    flex: 0 0 235px;
    padding: 15px 10px 0;
    background-color: #27187E;
    color: #fff;
}

.cv-head {
    flex: 0 0 230px;
}

.cv-profile {
    height: 150px;
    width: 150px;
    border: 7px solid #ffffff;
    border-radius: 50%;
}

.cv-cont-item--icon,
.cv-social--item {
    height: 30px;
    width: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    border-radius: 50%;
    border: 0.5px solid #ddd;
    /* background-color: #0089ff; */
    /* color: #fff; */
}

.cv-contact--item {
    flex: 0 0 40px;
}

.cv-cont-item--value {
    margin-left: 10px;
    width: calc(100% - 42px);
}

.cv-contact--item a,
.cv-social--item a,
.cv-menu--title a {
    text-decoration: none;
    color: #555;
}

.cv-menu--content ul {
    margin: 0;
}

.cv-contact--item:hover a,
.cv-social--item:hover a,
.cv-menu--title:hover a {
    text-decoration: underline;
    color: #1f86e7;
}

.cv-menu {
    padding: 0 10px;
    text-align: justify;
}

.cv-name,
.cv-designation,
.cv-head--title,
.cv-menu--title,
.cv-h1 {
    margin: 0;
}

.cv-menu--item {
    flex: auto;
}

.cv-content--right {
    padding-top: 20px;
    background-color: #e3f2fd;
}

.icon--disable {
    color: #eeeeee;
    opacity: 0.2;
}

.cv-cont-item--value {
    /* flex: 2; */
    flex: 1;
}

.cv-cont-item--value .fa {
    font-size: 13px;
}

.cv-menu--item {
    margin-bottom: 10px;
}

.cv-social--list {
    flex: 0 0 60px;
}